@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates&display=swap);
*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #4a4453;
  overflow-x: auto;
  overflow-y: hidden;
}
#root {
  overflow-x: auto;
  overflow-y: hidden;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  width: 100%;
  height: 30px;
  background-color: #524b5c;
}

#nav-container {
  width: 680px;
  height: 100%;
  padding: 0;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

#container {
  display: inline-block;
  position: relative;
  width: 600px;
  height: 100%;
}

#container #contact-li {
  border-right: 2px solid white;
}

#container #contact-li:hover {
  background-color: rgba(101, 101, 109, 0.761);
}

#container #resume-li:hover {
  background-color: rgba(101, 101, 109, 0.761);
}

#container #resume-button,
#container #contact-button {
  font-size: 16px;
  font-family: "Source Code Pro", monospace;
  color: white;
  font-weight: 400;
  background-color: transparent;
  width: auto;
  height: 100%;
  border: none;
  border-radius: 0px;
}

#container #resume-popup {
  position: absolute;
  background-color: gray;
  width: 150px;
  height: 0px;
  padding: 0;
  margin: 0;
  font: "Source Code Pro", monospace;
  font-weight: 600;
  font-size: 15px;
  color: transparent;
  overflow: hidden;
  top: 30px;
  right: 25%;
  transition: all 0.3s;
  border: 2px solid white;
  border-top: none;
}

#container #resume-popup p {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}

#container #contact-popup {
  position: absolute;
  background-color: gray;
  width: 0px;
  height: 30px;
  padding: 0;
  margin: 0;
  font: "Source Code Pro", monospace;
  font-weight: 600;
  font-size: 15px;
  overflow: hidden;
  top: 0px;
  left: 100%;
  transition: width 0.5s, height 0.5s 500ms;
}

#container #contact-popup form {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 280px;
  margin: auto;
}

#container #contact-popup form input {
  width: 100%;
  height: 30px;
  margin: 3px 0;
}

#container #contact-popup form textarea {
  width: 100%;
  height: auto;
  margin: 3px 0;
  resize: none;
}

#container #contact-popup h3 {
  width: 100%;
  text-align: center;
  height: 30px;
  margin: 3px 0;
}

#logo {
  vertical-align: top;
  display: inline-block;
  width: 80px;
  font-size: 30px;
  height: 100%;
  line-height: 90%;
  color: #837c8e;
  padding-right: 30px;
  cursor: pointer;
}

#nav-ul {
  text-decoration: none;
  list-style: none;
  height: 100%;
}

#nav-ul li {
  display: inline-block;
  text-align: center;
  height: 100%;
  width: 25%;
}

#nav-ul li a {
  padding-top: 5px;
  text-align: center;
  height: 100%;
  font-size: 16px;
  font-family: "Source Code Pro", monospace;
  color: white;
  font-weight: 400;
  text-decoration: none;
  display: inline-block;
}

#nav-ul li a::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background-color: #00c9a5;
  transition: width 0.3s ease-out !important;
}

#nav-ul li a:hover::after {
  width: 100%;
}
